<template>
  <v-container
    fluid
  >
    <v-toolbar
      flat
    >
      <v-toolbar-title>Data Transaksi</v-toolbar-title>
      <v-spacer/>
      <v-toolbar-items>
        <v-btn
          flat
          @click="$router.push('/app/data/add')"
        >
          <font-awesome-icon
            icon="plus"
            size="2x"
          ></font-awesome-icon>
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-data-table
      :headers="tableHeaders"
      :items="items"
      :total-items="items.length"
      hide-actions
      class="my-3"
    >
      <template
        v-slot:items="props"
      >
        <td>
          {{ props.item.tanggal }}
        </td>
        <td>
          {{ props.item.produksi }}
        </td>
        <td>
          {{ props.item.penjualan }}
        </td>
        <td>
          {{ props.item.persediaan }}
        </td>
        <td>
          {{ props.item.permintaan }}
        </td>
        <td>

          <v-btn small flat dark icon color="blue" :to="`/app/data/${props.item.id}/edit`">
            <font-awesome-icon icon="edit"></font-awesome-icon>
          </v-btn>
          <v-btn small flat dark icon color="red" @click="deleteData(props.item.id)">
            <font-awesome-icon icon="trash"></font-awesome-icon>
          </v-btn>

          <!-- <v-menu>
            <template v-slot:activator="{ on }">
              <font-awesome-icon
                v-on="on"
                icon="ellipsis-h"
                size="2x"
                class="grey--text"></font-awesome-icon>
            </template>
            <v-list dense>
              <v-list-tile :to="`/app/data/${props.item.id}/edit`">
                <v-list-tile-sub-title>
                  Update
                </v-list-tile-sub-title>
              </v-list-tile>
              <v-list-tile @click="deleteData(props.item.id)">
                <v-list-tile-sub-title>
                  Delete
                </v-list-tile-sub-title>
              </v-list-tile>
            </v-list>
          </v-menu> -->
        </td>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import Vue from "vue";
import tableHeaders from "./tableHeaders";
import TransaksiAPI from "@/services/transaksi";
import evbus from "@/evbus";

const _descriptions = () => (
  [
    {
      count: 120,
      text: "Total Data",
      color: "green"
    },
    {
      count: 34,
      text: "Rendah",
      color: "blue"
    },
    {
      count: 45,
      text: "Sedang",
      color: "indigo"
    },
    {
      count: 28,
      text: "Tinggi",
      color: "brown"
    }
  ]
)

export default Vue.extend({
  name: "DataList",
  data: () => ({
    descriptions: _descriptions(),
    tableHeaders,
    items: []
  }),
  methods: {
    _dateDisplay (x) {
      return {
        ...x,
        tanggal: x.tanggal.format("dddd, MMMM Do YYYY")
      }
    },
    async loadData() {
      evbus.$emit("loading-on");
      try {
        const data = await TransaksiAPI.find();
        this.items = data.map(x => this._dateDisplay(x));
      } catch (err) {
        console.log(err);
      }
      evbus.$emit("loading-off");
    },
    deleteData(id) {
      const message = "Apakah anda yakin akan menghapus data #" + id;
      const action = async () => {
        try {
          await TransaksiAPI.delete(id);
          evbus.$emit("success", "Sukses menghapus data");
        } catch (err) {
          evbus.$emit("error", "Gagal menghapus data");
        } finally {
          evbus.$emit("loading-off");
          this.loadData();
        }
      };
      evbus.$emit("app-action", { message, action });
    }
  },
  mounted() {
    this.loadData();
  }
});
</script>

